import React from 'react';
import ReactDOM from 'react-dom';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { tomorrow } from 'react-syntax-highlighter/dist/esm/styles/prism';
import 'bootstrap/dist/css/bootstrap.css';
import 'moment/moment.js'
import './index.css';
import moment from 'moment/moment.js';

class CodeBlock extends React.PureComponent {
  static defaultProps = {
    language: null,
  };

  render() {
    const { language, value } = this.props;
    return (
      <SyntaxHighlighter language={language} style={tomorrow}>
        {value}
      </SyntaxHighlighter>
    );
  }
}

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      now: moment(),
    };
  }

  componentDidMount() {
    window.setInterval(function () {
      this.setState({
        now: moment(),
      });
    }.bind(this), 1000);
  }

  render() {
    const birthday = moment("1999-01-28", "YYYY-MM-DD");
    const now = this.state.now;
    const age = now.diff(birthday, 'years');

    return (
      <div className="container">
        <h1>Hello, World!</h1>
        <CodeBlock language="cpp" value={
          `#include <iostream>

using std::string;

int main() {
  string name {"Simone"};
  string surname {"Ragusa"};
  int age {` + age + `};

  std::cout << "Today is ` + now.format("MMMM Do YYYY") + `" << std::endl
            << "Time is ` + now.format("HH:mm:ss") + `"           << std::endl;

  std::cout << "My name: "    << name    << std::endl
            << "My surname: " << surname << std::endl
            << "My age: "     << age     << std::endl;

  std::cout << "I love programming and developing websites."             << std::endl
            << "I am a student of computer engineering in Padua, Italy." << std::endl;

  return 0;
}`
        } />
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Page />,
  document.getElementById('root')
);
